
const truncateStringInMiddle = (text, startChars, endChars, maxLength) => {
    if (text.length > maxLength) {
        var start = text.substring(0, startChars);
        var end = text.substring(text.length - endChars, text.length);
        return start + '...' + end;
    }
    return text;
}

const truncateString = (text, maxLength) => {
    if (text.length > maxLength) {
        var start = text.substring(0, maxLength);
        return start + '...';
    }
    return text;
}

export {truncateStringInMiddle, truncateString}