import { useState, Fragment, Component, forwardRef } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

class WithdrawModal extends Component {

    constructor(props, ref){
        super(props)
        
    }

    state = {
        crypto: ""
    }

    handleCryptoChange = (event) => {

        this.setState(
            {
                crypto: event.target.value
            }
        );
    }

    render(){
        return (
            <Box sx={this.props.style_modal}>
                <Typography id="modal-modal-title" variant="h5" component="h2">
                    Withdraw from your account
                </Typography>
                <form role="form" className="mt-3">
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">Select crypto</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={this.state.crypto}
                            label="crypto"
                            onChange={this.handleCryptoChange}
                        >
                            <MenuItem value="udhdjhgjghj">Polkadot</MenuItem>
                            <MenuItem value="hfghljkfhgh">Kusama</MenuItem>
                            <MenuItem value="jhkdjfsdjgk">USDT</MenuItem>
                        </Select>
                    </FormControl>
                    <div className="row">
                        <div className="col-12">
                            <div className="form-group mb-4"> <label data-toggle="tooltip" title="Three digit CV code on the back of your card">
                                    <h6>Amount <i className="fa fa-question-circle d-inline"></i></h6>
                                </label> <input type="text" required className="form-control"/> </div>
                        </div>
                    </div>
                    <div className="card-footer"> <button type="button" className="subscribe btn btn-block shadow-sm" style={{backgroundColor: '#80BF75', color: 'white'}}> Retirer </button></div>
                </form>
            </Box>
        )
    }

    
};

export default forwardRef((props, ref) => <WithdrawModal {...props} forwardedRef={ref} />);