import * as React from 'react';
import { Fragment } from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import PronosTemplate from '../internes/pronos-template';
import {ArrowDropDown} from '@mui/icons-material';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

function SeeMore(props){
  return (
    <Fragment>
      <div className="row justify-content-center mt-3">
          <div className="col-auto">
            <button style={{ backgroundColor: '#80BF75', padding: '7px', width: 'fit-content', border: '0px solid', borderRadius: '5px', color: 'white'}}>Voir tout </button>
          </div>
      </div>
    </Fragment>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

export default function PronosHomeTabs() {
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  return (
    <Box sx={{ width: '100%'}}> 
      <AppBar position="static" sx={{ backgroundColor : '#80BF75' }}>
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="secondary"
          textColor="inherit"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label="Derniers pronostics" {...a11yProps(0)} />
          <Tab label="Pronostic préférés" {...a11yProps(1)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <div className="row justify-content-around">
            <PronosTemplate></PronosTemplate>
            <PronosTemplate></PronosTemplate>
          </div>
          <SeeMore></SeeMore>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <div className="row justify-content-around">
            <PronosTemplate></PronosTemplate>
            <PronosTemplate></PronosTemplate>
          </div>
          <SeeMore></SeeMore>
        </TabPanel>
      </SwipeableViews>
    </Box>
  );
}
