import { Fragment, useEffect, useState } from 'react';
import { authSubscriber } from '../../services/auth';
import { Link, NavLink } from "react-router-dom";
import MenuAuth from './menu-auth';

const MenuPc = (props) => {

    const [auth, setAuth] = useState(null);

    useEffect(() => {
        authSubscriber.subscribe((_auth) => {
            setAuth(_auth);
        });
    }, [])

    let menu;

    if(auth){
        menu = (
            <Fragment>
                <div className="col-6">
                    <div className="row p-0 justify-content-center">
                        <nav className="pp-menu">
                            <ul className="pp-wrap-menu-items">
                                <li className="pp-menu-item text-indigo"><Link to="/wallet">Wallet</Link></li>
                                <li className="pp-menu-item text-indigo"><Link to="/parieurs">Parieurs</Link></li>
                                <li className="pp-menu-item text-indigo"><Link to="/derivatives">Derivatives</Link></li>
                                <li className="pp-menu-item text-indigo"><Link to="/community">Community</Link></li>
                                <li className="pp-menu-item text-indigo"><Link to="/support">Support</Link></li>
                            </ul>
                        </nav>
                    </div>
                </div>
                <div className="col-4">
                    <MenuAuth></MenuAuth>
                </div>
            </Fragment>
        )
    }
    else{
        menu = (
            <Fragment>
                <div className="col-6">
                    <div className="row p-0 justify-content-center">
                        <nav className="pp-menu">
                            <ul className="pp-wrap-menu-items">
                                <li className="pp-menu-item text-indigo"><Link to="/community">Community</Link></li>
                                <li className="pp-menu-item text-indigo"><Link to="/support">Support</Link></li>
                            </ul>
                        </nav>
                    </div>
                </div>
                <div className="col-4">
                    <MenuAuth></MenuAuth>
                </div>
            </Fragment>
        )
    }

    return menu
}

export default MenuPc;