import logo from './logo.svg';
import './App.css';
import { useState, useEffect } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from "./components/home/home.js";
import Derivatives from "./components/derivatives/derivates";
import Header from "./components/header.js";
import Footer from "./components/footer.js";
import Wallet from "./components/wallet/wallet.js";
import Commnunity from './components/community/community';
import ListPros from './components/pros/listPros';
import Pro from './components/pros/pro';
import Support from './components/support/support';
import {
  useLocation,
  useNavigate,
  useParams
} from "react-router-dom";
import { authSubscriber } from './services/auth';
import Parieurs from './components/parieurs/parieurs';
import Profile from './components/profile/profile';

function App() {

  const [auth, setAuth] = useState(null);

  // Gets called on mount all the time
  useEffect(() => {
    console.log('component will unmount')
    authSubscriber.subscribe((_auth) => {
      console.log('authSubscriber App Component')
        setAuth(_auth);
    });
  }, []);

  // ONLY trigger when a certain state or prop values gets updated

  /* useEffect(() => {
    // Side-effect logic gets executed when values in array
    // only get updated
  }, [arg]); */

  if(!auth){
    return (
      <BrowserRouter>
          <Header/>
          <Routes>
            <Route path="/" element={<Home auth={auth} />}/>
            <Route path="/polkadot" element={<Home auth={auth} />}/>
            <Route path="/community" element={<Commnunity />} />
            <Route path="/support" element={<Support />} />
          </Routes>
          <Footer/>
      </BrowserRouter>
      
    );
  }

  return (
    <BrowserRouter>
        <Header/>
        <Routes>
          <Route path="/" element={<Home />}/>
          <Route path="/polkadot" element={<Home />}/>
          <Route path="/wallet" element={<Wallet />} />
          <Route path="/derivatives" element={<Derivatives />} />
          <Route path="/parieurs" element={<Parieurs />} />
          <Route path="/profile" element={<Profile />} />
          <Route path="/community" element={<Commnunity />} />
          <Route path="/support" element={<Support />} />
          <Route path="/list-pro/:sport" element={<ListPros />}/>
          <Route path="/pro/:id" element={<Pro />} />
        </Routes>
        <Footer/>
    </BrowserRouter>
    
  );
}

export default App;
