import { Fragment, useEffect, useState } from 'react';
import { authSubscriber, authSubscriberService } from '../../services/auth';
import { Link, NavLink } from "react-router-dom";
import AccountCircle from '@mui/icons-material/AccountCircle';
import {
    useLocation,
    useNavigate,
    useParams
  } from "react-router-dom";
const MenuAuth = (props) => {

    const [auth, setAuth] = useState(null);
    const [showAccountDropdownMenu, setShowAccountDropdownMenu] = useState('d-none');

    const toggleAccountDropdownMenu = () => {
        setShowAccountDropdownMenu(showAccountDropdownMenu == 'd-none' ? '' : 'd-none');
        
    }

    const navigate = useNavigate();

    useEffect(() => {
        authSubscriber.subscribe((_auth) => {
            setAuth(_auth);
        });
    }, [])

    const login = () => {
        authSubscriberService.send(true);
    }

    const logout = () => {
        authSubscriberService.send(false);
        navigate('/');
    }

    const register = () => {
        authSubscriberService.send(false);
    }

    let menu;

    if(auth){
        menu = (
            <div className="row menu-buttons justify-content-center p-0 mr-0">
                <div className="col-auto p-0" >
                    <div className={`${showAccountDropdownMenu} position-absolute p-3 bg-white`} style={{borderRadius: '5px', width: 'fit-content', marginTop: '30px'}}>
                        <div className="">
                            <div className="col-auto">
                                <Link style={{color: '#80BF75', fontSize: '1.2rem'}} to="/profile">Profil</Link>
                            </div>
                            <div className="col-auto">
                                <a style={{color: '#80BF75', fontSize: '1.2rem'}} onClick={logout} >Déconnexion</a>
                            </div>
                        </div>
                    </div>
                    <a onClick={toggleAccountDropdownMenu}>
                        <AccountCircle style={{color: '#80BF75', fontSize: '35px'}}></AccountCircle>
                    </a>
                </div>  
            </div>
        )
    }
    else{
        menu = (
            <div className="row menu-buttons justify-content-center p-0 mr-0">
                <div className="col-auto p-0"><a className="button" onClick={login} >Login</a></div>  
                <div className="col-auto p-0"><a className="button" onClick={register} >Register</a></div>
            </div>
        )
    }

    return menu
}

export default MenuAuth;