
import { Fragment, useState, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { Link, NavLink } from "react-router-dom";
import '../styles/header.css';
import Menu from '@mui/icons-material/Menu';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import ListItemButton from '@mui/material/ListItemButton';
import { authSubscriber, authSubscriberService } from '../services/auth';
import MenuPc from './internes/menu-pc';
import MenuAuth from './internes/menu-auth';

const Header = (props) => {

    const [auth, setAuth] = useState(null);
    const [state, setState] = useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
    });

    const menus = [
        {
            label: 'Wallet',
            to: '/wallet',
            mustAuthenticate: true
        },
        {
            label: 'Derivatives',
            to: '/derivatives',
            mustAuthenticate: true
        },,
        {
            label: 'Parieurs',
            to: '/parieurs',
            mustAuthenticate: true
        },
        {
            label: 'Community',
            to: '/community',
            mustAuthenticate: false
        },
        {
            label: 'Support',
            to: '/support',
            mustAuthenticate: false
        },
    ];

    let menusSide;
    if (auth) {
        menusSide = menus.map((m, index) => (
            <ListItem button key={m.label}>
                
              <ListItemButton>
                  <Link style={{
                  color: ' #80BF75', 
                  textTransform: 'capitalize',
                  fontWeight: '500',
                  fontSize: '1rem'}} to={m.to}>{m.label}</Link>
              </ListItemButton>
              
            </ListItem>
          ));
    } else {
        menusSide = menus.map((m, index) => {
            if(!m.mustAuthenticate){
                return (
                    <ListItem button key={m.label}>
                        <ListItemButton>
                            <Link style={{
                            color: ' #80BF75', 
                            textTransform: 'capitalize',
                            fontWeight: '500',
                            fontSize: '1rem'}} to={m.to}>{m.label}</Link>
                        </ListItemButton>
                    </ListItem>
                  )
            }else{
                return null;
            }
            
        });
    }
      

    // Gets called on mount all the time
    useEffect(() => {
        console.log('component will unmount')
        authSubscriber.subscribe((_auth) => {
        console.log('authSubscriber App Component')
            setAuth(_auth);
        });
    }, []);
    
      const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }
    
        setState({ ...state, [anchor]: open });
      };

    
      const list = (anchor) => (
        <Box
          sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
          role="presentation"
          onClick={toggleDrawer(anchor, false)}
          onKeyDown={toggleDrawer(anchor, false)}
        >
          <div className="col-2 logo-site">
            <Link to="/" ><img alt="logo OracleBet" src="/polkadot/logo.png"/></Link>
          </div>
          <Divider/>
          <List>
            {menusSide}
          </List>
        </Box>
      );
    
    return createPortal(
        <Fragment>
            <div className="app-nav-pc bg-white position-fixed w-100 top-0">
                <div className="row p-0 m-0">
                    <div className="col-2 logo-site">
                        <Link to="/" ><img alt="logo OracleBet" src="/polkadot/logo.png"/></Link>
                    </div>
                    <MenuPc></MenuPc>
                </div>
            </div>
            <div className="app-nav-mobile bg-white position-fixed w-100 top-0">
                <div className="row p-0 m-0">
                    <div className="col-2 logo-site">
                        <Menu style={{color: '#80BF75'}} onClick={toggleDrawer('left', true)}></Menu>
                        <Drawer
                            anchor='left'
                            open={state['left']}
                            onClose={toggleDrawer('left', false)}
                        >
                            {list('left')}
                        </Drawer>
                    </div>
                    <div className="col-2">
                        
                    </div>
                    <div className="col-8">
                        <MenuAuth></MenuAuth>
                    </div>
                </div>
            </div>
        </Fragment>, 
        document.getElementById("app-header")
    )
    
}

export default Header;